import { DragBlockProps, DragFragmentProps, DRAG_TYPE } from '../types';

/**
 * 判断 fragment 是否的议题区块
 * @param fragment
 * @returns
 */
function isSubjectFragment(fragment: DragFragmentProps) {
  if (fragment.type === DRAG_TYPE.blocks) {
    const first = fragment.fragment[0] as DragBlockProps;
    return !!first && first.type === 'heading-1' && !!first.nodeJson.data?.subjectPr;
  }

  return false;
}

export default isSubjectFragment;
